$c-black: #000000;
$c-white: #ffffff;
$bcg-c-white: #F6F7F7;
$bcg-c-black: #06071B;
$linear: linear-gradient(90.03deg, #00C5EB -1.97%, #B544FE 89.43%);
$placeholder: #939EAC;
// $h-f: transform background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);

body {
  font-family: 'Inter', sans-serif;
  color: $c-black;
  background-color: $bcg-c-white;

  font-weight: 500;
}

svg {
  display: block;
}

h1,
h2,
h3,
h4,
p,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
    margin: 0 auto;
    @media screen and (max-width:479px) {
        padding: 0 20px;
        width: 100%;
    }
    @media screen and (min-width:480px) {
        padding: 0 20px;
        width: 480px;
    }
    @media screen and (min-width:768px) {
        padding: 0 32px;
        width: 768px;
    }

    @media screen and (min-width:1280px) {
        max-width: 1280px;
        width: 100%;
        padding: 0 16px;
        width: 1280px;
    }
}

img {
  display: block;
  height: auto;
  width: 100%;
}

a {
  text-decoration: none;
}

.button {
  cursor: pointer;
  padding: 0;
  margin: 0;
  border-radius: 5px;
}
.btn-wrapper {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 1px;
    @media screen and (max-width:767px){
        align-self: center;
    }
    &:after {
            content: "";
            position: absolute;
            background: $linear;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            border-radius: 5px;
    }
}


.button-hf{
    position: relative;
    padding: 12px 60px 11px;
    border: 0px;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.1875;
    background: transparent;
    border-radius: 5px;
    color: $c-white;
    z-index: 2;
    &:hover {
        background: $bcg-c-black;
    }
    &:focus {
        background: $bcg-c-black;
    }
}

.section {
  margin: 0 auto;
  @media screen and (max-width: 479px) {
    padding: 40px 0;
    width: 100%;
  }
  @media screen and (min-width: 480px) {
    padding: 40px 0;
    width: 480px;
  }
  @media screen and (min-width: 768px) {
    padding: 80px 0;
    width: 768px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}

.title {
  text-align: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;

  @media screen and (min-width: 768px) {
    font-size: 24px;
    line-height: 1.21;
  }
}
